import React, {useState, useEffect} from "react";
import {Link} from "react-router-dom";
import axios from "axios";

export default function MyLists() {
  const [user, setUser] = useState({});

  const [userLists, setUserLists] = useState([]);

  //https://mohanned-todolistv2.herokuapp.com
  //specifies where to access the server and DB
  const instance = axios.create({
    baseURL: "http://localhost:3001"
  });

  //pulls user's saved List from DB and stores it in listArr
  function fetchUser() {
    instance
      .get("/auth/pullUser", {withCredentials: true})
      .then(function(response) {
        setUser(response.data);
        setUserLists(response.data.lists);
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  useEffect(fetchUser, []);

  function deleteList(e) {
    let listID = e.target.value;

    instance
      .delete("/api/" + listID, {
        withCredentials: true
      })
      .then(function(response) {
        setUserLists(response.data);
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  return (
    <div>
      <h1>Hello {user.name}, here are your lists:</h1>

      {userLists.length > 0 ? (
        userLists.map((element, index) => {
          return (
            <div key={index}>
              <p className="vis">
                {element.listName ? element.listName + " list" : "Unnamed list"}
                <Link
                  className="invis"
                  to={{
                    pathname: "/CreateList",
                    listId: element.listId,
                    save: true
                  }}
                >
                  <button variant="outlined">
                    <i className="fas fa-edit"></i>
                  </button>
                </Link>
                <button
                  className="invis"
                  value={element.listId}
                  onClick={deleteList}
                  type="submit"
                  name="deleteButton"
                >
                  <i className="fas fa-trash-alt"></i>
                </button>
              </p>
            </div>
          );
        })
      ) : (
        <p>You don't have any lists yet! Get started below!</p>
      )}

      <Link
        to={{
          pathname: "/CreateList",
          save: true
        }}
      >
        <button variant="outlined">Click here to create a new list.</button>
      </Link>

      <br />
      <br />

      <Link to="/">
        <button variant="outlined">
          Click here to return to the homepage.
        </button>
      </Link>
    </div>
  );
}
